<template>
  <div class="searchResult">
    <ul>
      <li class="search" v-show="searchVal != searchMusicFirstName">
        搜索“{{ searchVal }}”
      </li>
      <li
        v-for="item in result"
        :key="item.id"
        @click="$emit('searchResult', item.name)"
      >
        <i></i>
        <span>{{ item.name }}-{{ item | filterItem }}</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'SearchResult',
  props: {
    result: {
      type: Array,
      default: function () {
        return []
      }
    },
    searchVal: {
      type: String,
      default: function () {
        return '';
      }
    },
    searchMusicFirstName: {
      type: String
    }
  },
  filters: {
    filterItem (val) {
      if (val.artists) {
        return '歌曲'
      } else if (val.artist) {
        return '专辑'
      } else {
        return '歌手'
      }
    }
  }
}
</script>

